
import XHeader from 'vux/src/components/x-header/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import XHr from 'vux/src/components/x-hr/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Msg from 'vux/src/components/msg/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

//	var show1=true;
export default {
	components: {
		XHeader,
		Step,
		XButton,
		StepItem,
		XHr,
		XInput,
		Group,
		Msg,
		Divider,
		XButton
	},
	data() {
		return {
			step2: 0,
			PhoneNumber: '',
			password: '',
			password2: '',
			YzCode: '',
			show1: true,
			show2: false,
			show3: false,
			show4: true,
			//    nextStep:nextStep(),
			description: 'msg description',
			icon: '',
			buttons: [{
				type: 'primary',
				text: '现在去登录',
				link: '/User'
			}]
		}
	},
	methods: {
		change(val) {
			//    console.log(val)
		},
		getCode() {
			//  	var YzCode = Math.floor(Math.random () * 9000) + 1000;
			//  	console.log(YzCode);
			//  	return YzCode
			var _this = this;
			var mobile = _this.PhoneNumber;
			var title ="用户说明";
			this.$http.post("http://192.168.15.177/zrmall/User/User/send_mobile_verify", { mobile: mobile,title:title  }).then(function(res) {
				console.log(res.data)
			});
		},
		nextStep() {
			if(this.PhoneNumber == "" || this.YzCode == "" && this.YzCode == rand) {
				return false;
			} else {
				this.step2++
					var step = this.step2 + 1
				var _this = this
				if(step > 3) {
					return false;
				}
				if(step == 2) {
					console.log(_this.rand)
					this.$http.post("http://localhost:8080/register", {
						PhoneNumber: _this.PhoneNumber,
						rand: _this.YzCode
					}).then(function(res) {
						//      	 this.$router.push('/home')
						console.log("您注册的用户名是:" + res.data.PhoneNumber)
						console.log("验证码是:" + res.data.rand)
					})

					this.show1 = false;
					this.show2 = true;
				}
				if(step == 3) {
					this.show1 = false;
					this.show2 = false;
					this.show3 = true;
					this.show4 = false;
				}
				console.log(step);
			}
		},
		changeIcon() {
			if(!this.icon || this.icon === 'success') {
				this.icon = 'warn'
				return
			}
			if(this.icon === 'warn') {
				this.icon = 'info'
				return
			}
			if(this.icon === 'info') {
				this.icon = 'waiting'
				return
			}
			if(this.icon === 'waiting') {
				this.icon = 'success'
			}
		}
	}
}



