    methods: {
      clicks(key){
        this.index = key;
        if (key!=0||key!=1){
          this.content=""
          this.show=false
        }
        switch (key) {
          case 0:
            this.content = recommend
            this.show=true
            break;
          case 1:
            this.content = computer
            this.show=true
            break;
	        case 2:
	        this.content = phone
	        this.show=true
	        break;
        }
      },
    }
  }



