      , {
        src: require('./images/sp02.png'),
        name: '苹果6S plus',
        charge:'5888',
        text: '玫瑰红，32G，国产,请包装严实，妥善寄件',
        link:"/Home",
        numberValue:2
//      url: {
//        path: '/component/radio',
//        replace: false
//      }
      }
      ]
//求和
 var amount = 0;
  for(var i=0;i< prolist.length;i++){
   amount += +prolist[i].charge;
   }

export default {
  components: {
  	XHeader,
    Group,
    Cell,
    XNumber,
    Checker,
    CheckerItem,
    Divider,
    Panel,
    Swipeout, SwipeoutItem, SwipeoutButton,MyCheck
  },


  methods: {

  },
  data () {
    return {
      numberValue:0,
      error: '',
      prolist: prolist,
      amount,
      footer: {
        title: '查看更多',
        url: 'http://vux.li'
      }
    }
  },
//    computed: {
//    author () {
//      return this.$store.state.author
//    }
//  }


}



